@use "../variables.scss" as var;

.header {
  position: relative;
  z-index: 1;

  min-height: var.$site-navigation-toggle-size;
}

.header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: max-content;

  @media (min-width: var.$tablet-width) {
    z-index: 1;
  }
}
